@import url(//db.onlinewebfonts.com/c/1c45e28f8e86cc89876f003b953cc3e9?family=SF+Pro+Text);
.storybook-button {
  font-family: "SF Pro Text", 'Nunito Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  
  width: 180px;
  height: 40px;
  font-size: 17px;
  border: 0;
 /*border-radius: 3em;*/
  border-radius: 6px;
  line-height: 22px;
  cursor: pointer;
  display: inline-block;
  line-height: 1;
  margin-top: 15px;
  margin-bottom: 15px;
}
.storybook-button--default {
  color: white;
  background-color: #6979F8;
  
}
.storybook-button--ghost {
  border: 2px solid #6979F8;
  box-sizing: border-box;
  background-color: white;
  color:  #6979F8;
  /*box-shadow: rgba(0, 0, 0, 0.15) 0px 0px 0px 1px inset;*/
}
.storybook-button--small {
  font-size: 13px;
  line-height: 18px;
  padding: 10px 16px;
}
.storybook-button--medium {
  font-size: 14px;
  padding: 11px 20px;
}
.storybook-button--large {
  width: 343px;
  height: 48px;
  padding: 12px 24px;
}
